.Matrix__background-illumination {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.Matrix__background-illumination::before {
  position: relative;
  content: "";
  top: 0;
  left: 0;
}

.Matrix__background {
  z-index: -1;
  display: block;
  line-height: 0;
  position: absolute;
  width: 101%;
  top: -0.5%;
  left: -0.5%;
  background-color: rgb(0, 0, 0);
}

.Matrix__panel {
  background-color: rgba(0, 0, 0, 1);
  background-color: rgba(0, 255, 153, 0.3);
  border-radius: 45px;
  min-width: 300px;
  max-width: 600px;
  min-height: 300px;
  max-height: 600px;
  position: absolute;
  z-index: 1;

  display: none;
}

.Matrix__illumination {
  width: 100px;
  height: 100px;
  margin: 0;
  z-index: 1;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.4);
  background: radial-gradient(
    rgba(255, 0, 0, 0.4),
    rgba(255, 0, 0, 0.45),
    rgba(255, 0, 0, 0.5),
    rgba(255, 0, 0, 0.55),
    rgba(255, 0, 0, 0.6),
    rgba(255, 0, 0, 0.65),
    rgba(255, 0, 0, 0.7),
    rgba(255, 0, 0, 0.8),
    rgba(255, 0, 0, 0.9)
  );
  filter: blur(50px);

  position: absolute;
}
